export default {
	data() {
		return {
			// 储存页面高度
			screenHeight: '',
			// 左侧导航栏数据
			currentIndex: 0,
			leftDomData: [],
			// 元素的高度
			cateItemHeight: '',
			leftScrollTop: '',
			navList: [],
			// 右侧商品数据
			goodsList: [],
			rightDomData: [],
			rightScrollTop: 0
		};
	},
	watch:{
		currentIndex(newVal,oldVal) {
			// 获取每次滚动式左侧scroll-view的高度和滚动的高度scroll-top
			const query = uni.createSelectorQuery().in(this);
			query.select('#leftScroll').fields({
				size:true,
				scrollOffset:true
			},data => {
				let H = data.height;
				let SH = data.scrollTop;
				// 下边
				if((this.leftDomData[newVal] + this.cateItemHeight) > (H + SH)) {
					return this.leftScrollTop = this.leftDomData[newVal] + this.cateItemHeight - H
				}
				// 上边
				if(SH > this.leftDomData[newVal]) {
					this.leftScrollTop = this.leftDomData[newVal]
				}
			}).exec();
		}
	},
	methods: {
		// tab切换
		changeCurrentIndex(index) {
			this.currentIndex = index;
			this.rightScrollTop = this.rightDomData[index];
		},
		// 实现右侧滚动左侧响应
		rightScroll(event) {
			let top = event.detail.scrollTop;
			this.rightDomData.forEach((item,index) => {
				if(item <= (top)) {
					this.currentIndex = index
				}
			})
		},
		// 获取模拟数据
		getData() {
			for(let i = 0;i < 18;i++) {
				this.navList.push({
					name: `分类${i}`
				})
				this.goodsList.push({
					list:[]
				})
			}
			for(let j = 0;j < this.goodsList.length;j++) {
				for(let z= 0;z < 12;z++) {
					this.goodsList[j].list.push({
						name: `分类${j}商品${z}`,
						src: `/static/images/demo/cate_05.png`
					})
				}
			}
		},
		// 获取节点信息
		getDomRect() {
			const query = uni.createSelectorQuery().in(this);
			// 获取左侧导航栏各元素距离顶部的位置
			query.selectAll('.left-item').fields({
				size:true,
				rect:true
			},data => {
				this.leftDomData = data.map(item => {
					// 元素的高
					this.cateItemHeight = item.height;
					// 元素距离顶部的高度
					return item.top
				});
			}).exec();
			// 获取右侧商品栏各元素距离顶部的位置
			query.selectAll('.product-list').boundingClientRect(data => {
				this.rightDomData = data.map(item => {
					return item.top
				});
			}).exec();
		},
	},
	onLoad() {
		// 获取当前设备屏幕的可视高度
		uni.getSystemInfo({
			success: res => {
				this.screenHeight = res.windowHeight + 'px'
			}
		});
		// 初始化数据
		this.getData();
	},
	mounted() {
		this.getDomRect()
	}
}
